var that;
export default {
	data() {
		return {
			TabCur: 1,
			scrollLeft: 0,
			navtitle: [{ id: 1, name: '未发货' }, { id: 2, name: '已发货' }, { id: 3, name: '已签收' }],
			list: [],
			onlist: false,
			page: 1,
			onmore: false,
			access_token: '',
			modalName: '',
			subkdid: '',
			express_index: '',
			express_list: [],
			fhid:''
		};
	},
	methods: {
		tpinfo2(e) {
			uni.navigateTo({
				url: '../orderInfo/orderInfo?id=' + e.currentTarget.dataset.id
			});
		},
		hidden() {
			that.modalName = '';
		},
		prinexpress(e) {
			that.express_index = e.detail.value;
		},
		clickfh(e) {
			that.fhid=e.currentTarget.dataset.id;
			that.modalName = 'kuaidi';
		},
		prinCat(e) {
			that.express_index = e.detail.value;
		},
		showkdModel(e) {
			that.subkdid = e.currentTarget.dataset.id;
			that.modalName = 'kuaidi';
		},
		subkd() {
			that.hiddenModel();
		},
		tabSelect(e) {
			that.TabCur = e.currentTarget.dataset.id;
			that.page = 1;
			that.list = [];
			that.getorder();
		},
		fahuo(e) {
			let value = e.detail.value;
			let req = true,
				title = '';
			value.express ? '' : ((req = false), (title = '请选择快递公司'));
			value.express_no ? '' : ((req = false), (title = '请填写快递单号'));
			if (!req) {
				that.$showModal(title);
				return;
			}
			this.$request
				.request({
					url: that.api.anchor_order.send,
					data: {
						order_id: that.fhid,
						express: value.express,
						express_no: value.express_no
					},
					method: 'post'
				})
				.then(res => {
					console.log(res);
					that.hidden();
					that.$alertok('提交成功！');
					that.onlist = true;
					that.page=1;
					that.list=[]
					that.getorder();
				});
		},
		getorder() {
			that.onlist = true;
			this.$request
				.request({
					url: that.api.anchor_order.list,
					data: {
						type: that.TabCur,
						page: that.page
					}
				})
				.then(res => {
					console.log(res);
					let list = res;
					if (list.length > 0) {
						that.list = that.list.concat(list);
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了！');
								that.nomore = true;
							}
						} else {
							that.onlist = false;
						}
					}
				});
		},
		tpinfo(e) {
			uni.navigateTo({
				url: '../orderInfo/orderInfo?id=' + e.currentTarget.dataset.id
			});
		},
		 
	},
	onLoad(option) {
		that = this;
		that.TabCur = option.id ? option.id : 1;
		let config = uni.getStorageSync('config');
		that.express_list = config.express;
		that.getorder();
	}
};
